body {
  background-color: #2d2d2d;
  color: #FFE031;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.form-control {
    background-color: #396BBA;
    color: #FFE031;
}
.list-group .list-group-item{
    background-color: #396BBA;
    font-weight: bold;
    position: relative;
}

.btn {
  background-color: #f9a96d !important;
  color: #396BBA !important;
  position: fixed;
  top: 60vh;
  z-index: 1;
}

.btn:hover {
  background-color: #f9a96d !important;
  color: #396BBA;
}

.btn:active {
  background-color: #f9a96d !important;
  color: #FFE031;
}

.list {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
}

.list-child {
  flex: 1 0 30%;
  width: 250px;
}

.scroll {
  background-color: rgba(249, 169, 109, 0.5);
}


.navbar-light .navbar-nav .nav-link  {
    color: #FFE031;
    font-weight: bold;
}


